.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(192, 192, 192, 0.3);
}

.form {
  margin: 0 auto;
  border-radius: 8px;
  width: 250px;
  min-height: 150px;
  padding: 20px;
  background-color: #21262d;
  box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.22),
    0 10px 13px 0px rgba(0, 0, 0, 0.34);
}

.label {
  display: flex;
  margin-bottom: 5px;
  text-transform: capitalize;
  color: #ffffff;
}

.input {
  width: 230px;
  outline: transparent;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #30363d;

  background-color: #8b949e;
}

.input:focus {
  border: 1px solid #c9d1d9;
}

.wrapper {
  display: flex;
  justify-content: space-between;
}

.button {
  margin: 0 auto;
  min-width: 100px;
  max-width: 100%;
  padding: 15px;
  border-radius: 8px;
  background-color: rgba(150, 146, 146, 0.3);
  transition: background-color 250ms linear, color 250ms linear;
  font-weight: 600;
  color: #ffffff;
  cursor: pointer;
  border: transparent;
}

.button:hover {
  background-color: rgba(192, 192, 192, 0.3);
}
